body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.book-container {
  width: 1600px;
  margin: 10px auto;
}

.book-header {
  padding: 20px;
  border-left: 1px solid #cccc;
}

.book-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.book-item {
  width: 100%;
  height: 100%;
  max-width: 330px;
  min-height: 368px;
  margin: 20px 0;

  border-radius: 5px;
  box-shadow: 4px 7px 11px rgba(0,0,0,0.2);
}

.book-image-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 250px;
  position: relative;
}

.book-image-wrapper img {
  height: 200px;
  width: 200px;
}

.book-index {
  top: 0;
  left: 0;
  position: absolute;

  padding: 2px 5px;

  color: #fff;
  background: #C45500;
}

.book-price p,
.book-reviews p,
.book-author p,
.book-title p {
  font-size: 13px;
  text-align: center;
}